<template>
  <div class="Cashier">
    <Tabs
      v-model="tab_index"
      class="tab"
      size="small"
      @on-click="handleTabClick"
    >
      <TabPane
        v-for="item in tab_list"
        :key="item.id"
        :label="item.name"
        :name="item.name"
      >
        <CashierSet v-if="current(item, '')" />
        <Print v-if="current(item, '')" />
        <PrintSet v-if="current(item, '')" />
      </TabPane>
    </Tabs>
  </div>
</template>
<script>
  import CashierSet from '@/components/setting/system/cashier/cashier-set'
  import Print from '@/components/setting/system/cashier/print'
  import PrintSet from '@/components/setting/system/cashier/print-set'

  export default {
    name: 'Cashier',
    components: { CashierSet, Print, PrintSet },
    data: function () {
      return {
        tab_index: '',
        tab_list: [
          { name: '', id: 1 },
          { name: '', id: 2 },
          { name: '', id: 3 },
        ],
      }
    },
    computed: {
      current() {
        return (item, name) => {
          return name === item.name && this.tab_index === name
        }
      },
    },
    methods: {
      handleTabClick(index) {
        this.tab_index = index
      },
    },
  }
</script>
<style lang="less" scoped>
  .Cashier {
    background: white;
  }
</style>
